// Odometer

.odometer.odometer-auto-theme {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: inline;
  position: relative;
  font-family: $font-family-base;
  line-height: 1.1em;
  font-size: 32px;
  .odometer-digit {
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    *zoom: 1;
    *display: inline;
    position: relative;
    .odometer-digit-spacer {
      display: inline-block;
      vertical-align: middle;
      *vertical-align: auto;
      *zoom: 1;
      *display: inline;
      visibility: hidden;
    }
    .odometer-digit-inner {
      text-align: left;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      overflow: hidden;
    }
    .odometer-ribbon {
      display: block;
    }
    .odometer-ribbon-inner {
      display: block;
      -webkit-backface-visibility: hidden;
    }
    .odometer-value {
      display: block;
      -webkit-transform: translateZ(0);
    }
    .odometer-value.odometer-last-value {
      position: absolute;
    }
  }
  .odometer-value {
    text-align: center;
    color: $gray-900;
    font-weight: 600;
  }
}

